<template>
  <div id="slidebar">
    
    <el-header style="height:34px;">
     <div class="header">
       <span class="headerLeft">首页</span>
       <span class="headerRight">登陆</span>
     </div>
    </el-header>
    
    <div class="headerSearch">
      <div class="headerSearchMain">
        <img src="https://zhong-heart.github.io/asd/erling/logo.jpg" alt="ColourData">
        <div class="searchContent">
          <el-input v-model="inputSearch" placeholder="请输入内容" style="width:450px;display:inline-block;border:0;"></el-input>
          <el-button type="primary" icon="el-icon-search" style="display:inline-block;position:relative;right:5px;"></el-button>
        </div>
      </div>
    </div>
    <div id="main-content">
      <div id="aside">
        <ul>
          <li><p style="width:198px;height:50px;line-height:50px;display:inline-block;text-indent:50px;background:#E7E7E7;">  分类</p></li>
          <li><router-link to="/slidebar/tourist"><span>旅游酒店</span></router-link></li>
          <li><span>新闻资讯</span></li>
          <li><span>地方新闻</span></li>
          <li><span>财经投资</span></li>
          <li><span>科技创业</span></li>
          <li><span>地产物业</span></li>
          <li><span>汽车</span></li>
          <li><span>视觉</span></li>
        </ul>
        <div id="qrCode">
          <img src="https://zhong-heart.github.io/asd/erling/wechat.jpg" alt="">
          <p>扫描关注</p>
        </div>
       
      </div>
      <div id="content">
        <router-view></router-view>
      </div>
    </div> 
  </div>
</template>
<script>
import logo from './../img/logo.jpg'
export default {
  name: 'SlideBar',
 data() {
      return {
       
        inputSearch:''
      };
    },
   
  
}
</script>
<style <style scoped>
.router-link-active{
  color:yellowgreen;
}
a {
   text-decoration: none;

}

.el-header, .el-footer {
    background-color:#F1F1F1;;
    
    height: 34px;
    line-height: 34px;
  }

  #main-content{
   
    width: 1200px;
    margin:20px auto;
  }
  #aside{
    width: 200px;
    float: left;
  }
  #aside ul{
    list-style: none;
  }
  #aside ul li span{
    width:198px;
    height:42px;
    line-height:42px;
    display:inline-block;
    text-indent:50px;
    background: #F5F5F5;
  }
  #aside ul li:hover span{
    background: #ffffff;
  }
  #aside #qrCode{
    background: #F5F5F5;
    margin-top: 10px;
    width: 198px;
    height: 240px;
    text-align: center;
  }
  #aside #qrCode img{
    width: 150px;
    margin-top: 10px;
  }
  #aside #qrCode p{
    
  }
  #content{
    /* border: 1px solid red; */
    width: 980px;
    float: right;

  }

  #slidebar{
    color: #5b5b5b;
    font-family: "微软雅黑,宋体";
    font-size: 14px;
  }
  .header{
    width: 1200px;
    height: 34px;
    margin: auto;
  }
  .headerLeft{
    float: left;
    line-height: 34px;
  }
  .headerRight{
    float: right;
    line-height: 34px;
  }
  .headerSearch{
    width: 100%;
    height: 100px;
    border-bottom: 1px solid #e6e6e6; 
  }
  .headerSearchMain{
    width: 1200px;
    margin: auto;
  }
  .headerSearchMain img{
    float: left;
    width: 160px;
    padding-top:10px;
  }
  .searchContent{
    float: left;
    margin: 25px 200px; 
    
  }
</style>



