<template>
  <div id="Login">
    <div id="Content">
      <div id="form"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Login'
}
</script>
<style>

#Content{
  width: 100%;
  height: 100vh;
  /* border: 1px solid rebeccapurple; */
  position: absolute;
  background: powderblue;
 
}
#form{
  width: 500px;
  margin:200px auto;
  border: 1px solid red;
  background: greenyellow;
  border-radius: 10px;
}

</style>



