<template>
  <div id="Tourist">
   
    <div class="selectLabel">
      <div class="selectLabelTitle">
        按账号内容风格
      </div>
      <el-input size="small" style="display:inline-block;width:300px;margin-left:100px;" v-model="input" placeholder="请输入内容"></el-input>
      <el-button size="small" type="success" plain icon="el-icon-search" style="position:relative;right:10px;"></el-button>
    </div>
    <div class="selectLabel">
      <div class="selectLabelTitle">
        （按内容搜索时）
      </div>
     <el-tag
      :key="tag"
      v-for="tag in dynamicTags"
      closable
      :disable-transitions="false"
      @close="handleClose(tag)"
      style="margin:0 10px;">
      {{tag}}
    </el-tag>
    </div>
    <div class="selectLabel">
      <div class="selectLabelTitle">
        行业：
      </div>
      <div style="display:inline-block;">
        <el-radio-group v-model="state.radioIndustry" size="small">
          <el-radio-button label="0">汽车</el-radio-button>
          <el-radio-button label="1">母婴</el-radio-button>
          <el-radio-button label="2">美妆</el-radio-button>
          <el-radio-button label="3">运动</el-radio-button>
          <el-radio-button label="4">文艺</el-radio-button>
          <el-radio-button label="5">服装</el-radio-button>
          <el-radio-button label="6">游戏</el-radio-button>
          <el-radio-button label="7">段子</el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <div class="selectLabel">
      <div class="selectLabelTitle">
        粉丝数：
      </div>
      <div style="display:inline-block;">
        <el-radio-group v-model="state.radioFans" size="small">
          <el-radio-button label="0">10万以下</el-radio-button>
          <el-radio-button label="1">10-20万</el-radio-button>
          <el-radio-button label="2">20-40万</el-radio-button>
          <el-radio-button label="3">40-60万</el-radio-button>
          <el-radio-button label="4">60-100万</el-radio-button>
          <el-radio-button label="5">100万以上</el-radio-button>
          
        </el-radio-group>
      </div>
    </div>
    <div class="selectLabel">
      <div class="selectLabelTitle">
        价格：
      </div>
      <div style="display:inline-block;">
        <el-radio-group v-model="state.radioPrice" size="small">
          <el-radio-button label="0">100元以下</el-radio-button>
          <el-radio-button label="1">10-500元</el-radio-button>
          <el-radio-button label="2">500-100元</el-radio-button>
          <el-radio-button label="3">1000-2000元</el-radio-button>
          <el-radio-button label="4">2000-5000元</el-radio-button>
          <el-radio-button label="5">5000元以上</el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <div class="selectLabel">
