      </div>
      <div style="display:inline-block;">
        职业：
              <template>
                <el-select  size="mini" v-model="state.valueJob" placeholder="请选择" style="width:120px;">
                  <el-option
                   
                    v-for="item in jobOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </template>
        性别：
              <template>
                <el-select  size="mini" v-model="state.valueSex" placeholder="请选择" style="width:85px;">
                  <el-option
                   
