                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </template>
        地域：
              <template>
                <el-select size="mini" v-model="state.valueTerritory" placeholder="请选择" style="width:100px;">
                  <el-option-group
                    v-for="group in territoryOptions"
