<template>
  <div id="PersonDetail">
    <div id="personInfo">
      <div id="personInfoLeft">
        <div id="personHead">
          <img src="https://zhong-heart.github.io/asd/erling/logo.jpg" alt="">
        </div>
        <div id="personName">
          <p><strong style="font-size:18px;">Name回忆专用小马甲</strong></p>
          <p>微博认证：<span>微博人气博主</span></p>
          <p>    简介：<span>《原无岁月可回头》</span></p>
        </div>
        <div style="clear:both;">
          <p style="text-indent:30px;">风格： 逗比 爱宠</p>
          <p style="text-indent:30px;">分类： 段子手 生活类</p>
          <p>本次数据统计时间： 2017/1/1 0：00：00</p>
        </div>
      </div>
      <div id="personInfoRight">
        <div>评分：<el-rate
                v-model="NewGrade"
                show-text>
              </el-rate>
        </div>
        <div>历史最高分：<el-rate
                v-model="OldGrade"
                show-text>
              </el-rate>
        </div>
        <p style="padding:5px 0;">粉丝数：299999999</p>
      </div>
    </div>
    <div id="echarts1">
      <div style="width:800px;height:300px;float:left;">
        <img style="width:600px;height:400px;" src="https://zhong-heart.github.io/asd/erling/charts1.png" alt="">
      </div>
      <div style="width:200px;height:300px;float:left;">
        <p style="text-align:center;font-size:18px;margin:5px 0;">曾合作的类和品牌:</p>
        <div class="echartsright" v-for="item in [1,2,3,4,5]">
          <span class="typeL">荣威</span>
          <span class="typeR">28{{item}}</span>
        </div>
        <div class="echartsright" v-for="item in [1,2,3,4,5]">
          <span class="typeL">自然堂</span>
          <span class="typeR">18{{item}}</span>
        </div>
      </div>
    </div>
    <div id="echarts2">
      <div style="width:400px;float:left;">
        <p>粉丝分析</p>
        <img style="width:380px;" src="https://zhong-heart.github.io/asd/erling/echarts2.png" alt="">
      </div>
      <div style="width:400px;float:left;">
        <p>地域top5</p>
        <img style="width:380px;" src="https://zhong-heart.github.io/asd/erling/echart3.png" alt="">
      </div>
      <div style="width:400px;float:left;">
        <img src="https://zhong-heart.github.io/asd/erling/ciyun.jpg" alt="">
      </div>
    </div>
    <div id="recommend" >
      <h4 style="width:100%;height:40px;">类似账号推荐</h4>
      <div  style="display:flex;flex-wrap:wrap;">
        <div class="fans" v-for="item in [1,2,3,4,54,5,4,4]">
          <img src="https://zhong-heart.github.io/asd/erling/ciyun.jpg" alt="">
          <p>ColourData</p>
          <p>简介</p>
          <p>粉丝数：23232323</p>
        </div>
        
      </div>
    </div>
    <div id="Recent">
          <h4>近期微博文章</h4>
          <div>
            <img style="width:50px;height:50px;border-radius:100%;float:left;" src="https://zhong-heart.github.io/asd/erling/ciyun.jpg" alt="">
            <span style="padding:50px 0 0 30px;">账号昵称</span>
          </div>
          <p style="margin:40px 0 0;">发帖时间  发帖状态   <span style="padding: 0  30px;float:right;">转发数</span><span style="padding:0 30px;float:right;">评论数</span><span style="padding:0 30px;float:right;">转发数</span></p>
      </div>
    </div>  

  </div>
</template>
<script>
export default {
  name:'PersonDetail',
  data(){
    return{
      NewGrade:3,
      OldGrade:4
    }
  }
}
</script>
<style>
#PersonDetail{
  font-size: 14px;
}
#personInfo{
  width: 1200px;
  margin: 10px auto;
  height: 150px;
  /* border: 1px solid red; */
  padding: 10px;
  background: #F4F8FC;

}
#personInfoLeft{
  width: 800px;
  float: left;
}
#personInfoRight{
  width: 390px;
  float: right;
}
#personHead{
  width: 90px;
  height: 70px;
  float: left;
}
#personHead img{
  width: 100%;
  height: 100%;
}
#personName{
  width: 500px;
  float: left;
  padding-left: 50px;
}
#echarts1{
  width: 1200px;
  margin: auto;
  height: 450px;
  background: #F4F8FC;
  padding: 10px;
  
}
.echartsright{
  height: 30px;
  font-size: 16px;
  width: 180px;
  border-bottom: 1px solid darkgray;
  padding: 0 10px;
}
.typeL{
  float: left;
}
.typeR{
  float: right;
}
#echarts2{
  width: 1200px;
  margin: auto;
  height: 400px;
}
#recommend{
  width: 1200px;
  padding: 10px;
  background: #F4F8FC;
  margin: auto;
  height: 230px;
}
.fans{
  width: 300px;height: 100px;
}
.fans img{
  width: 50px;
  height: 50px;
  border-radius: 100%;
  padding: 10px;
  float: left;
}
#Recent{
  width:1200px;
  margin:5px auto;
  padding: 10px;
  background:#F3F7F9;
  height: 100px;
}
</style>



