import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from './../components/HelloWorld'
import SlideBar from './../views/slidebar/slidebar'
import Tourist from './../views/content/tourist'
import About from './../views/content/about'
import Login from './../views/login/login'
import PersonDetail from './../views/content/persondetail'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'slidebar'
    },
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/login',
      name:'Login',
      component:Login
    },
    {
      path:'/persondetail',
      name:'PersonDetail',
      component:PersonDetail
    },
    {
      path:'/slidebar',
      name:SlideBar,
      component: SlideBar,
      children:[
        {
          path:'tourist',
          name:Tourist,
          component:Tourist
        },
        {
          path:'about',
          name:About,
          component:About,
        },
        {
          path:'/',
          redirect:'tourist'
        }
      ]
    }
    
  ]
})



